@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?fflti3');
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?fflti3#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?fflti3') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?fflti3') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?fflti3##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
}

